import './App.css';
import  {useState} from 'react'
import  {useEffect} from 'react'
import { BrowserRouter as  Router , Route } from 'react-router-dom';
import Header from './components/Header';
import Tasks from './components/Tasks';
import AddTask from './components/AddTask';
import Footer from './components/Footer';
import About from './components/About';





function App() {

  const [showAddTask , setShowAddTask] = useState (false)
  
  const [tasks , setTasks] = useState ([])



  // Load data on Loading 
  useEffect(()=>{
    const getTasks = async ()=>{

      const tasksFromServer = await loadData ()
      setTasks(tasksFromServer);

    }
    getTasks();
  },[])


  // fetch tasks from server 

  const loadData = async ()=>{
    const res = await fetch ('http://localhost:5000/tasks');
    const data = await res.json();
    return data 
  }

  // fetch tasks from server 

  const loadDataForTask = async (id)=>{
    const res = await fetch (`http://localhost:5000/tasks/${id}`);
    const data = await res.json();
    return data 
  }


// Delete Task 

    const deleteTask = async (id)=>{
      await fetch (`http://localhost:5000/tasks/${id}`,
      {
        method : 'DELETE'
      })
      
      setTasks(tasks.filter((task)=>task.id !== id))
    }
    
    
    // toggle reminder
    
    const toggleReminder = async (id)=>{
      
      const taskToToggle = await loadDataForTask(id)
      const upTask = {...taskToToggle, reminder : !taskToToggle.reminder}
      
      const res = await fetch (`http://localhost:5000/tasks/${id}`,{
        method : 'PUT',
        headers : {
          'Content-type': 'application/json'
        },
        body : JSON.stringify(upTask)
      })
      
      const data = await res.json()
      
      setTasks(tasks.map((task)=>task.id===id ? {...task , reminder: data.reminder } : task))
    }
    
    // Add Task 
    
    const addTask = async (task)=>{
      const res = await fetch ('http://localhost:5000/tasks' , {
        method : 'POST' , 
        headers : {'Content-type' : 'application/json'},
        body : JSON.stringify(task),
      })
      
      const data = await res.json ()
      
      setTasks([...tasks , data])
    }
    
    return (
      <Router>
      <div style = {styling} className="container ">
      <Header showAdd = {showAddTask} onAdd={ ()=>{setShowAddTask (!showAddTask) }}  title = {'Tracker'}/>
      <Route path='/about' component={About} />
      <Route path='/' exact render={(props)=>(
        <>
           {showAddTask && <AddTask onAdd = {addTask} />}
      {tasks.length > 0 ? <Tasks tasks ={tasks} onDelete={deleteTask} onDoubleClick={toggleReminder} /> : <h4 style={{textAlign: 'center'}}> No Tasks To Show</h4>}
        </>
      )}/>
      <Footer />
    </div>
    </Router>
  );

}

const styling = {
  backgroundColor:'lightgrey',
  color:'black',
  border: '3px solid lightgreen'
}

export default App;
